import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import shipinkechengList from '@/views/pages/shipinkecheng/list'
import shipinkechengDetail from '@/views/pages/shipinkecheng/formModel'
import shipinkechengAdd from '@/views/pages/shipinkecheng/formAdd'
import storeupList from '@/views/pages/storeup/list'
import tuwenkechengList from '@/views/pages/tuwenkecheng/list'
import tuwenkechengDetail from '@/views/pages/tuwenkecheng/formModel'
import tuwenkechengAdd from '@/views/pages/tuwenkecheng/formAdd'
import kechengleixingList from '@/views/pages/kechengleixing/list'
import kechengleixingDetail from '@/views/pages/kechengleixing/formModel'
import kechengleixingAdd from '@/views/pages/kechengleixing/formAdd'
import exampaperList from '@/views/pages/exam/exampaperList'
import examination from '@/views/pages/exam/examination'
import examrecordList from '@/views/pages/exam/examrecordList'
import examfailrecord from '@/views/pages/exam/examfailrecord'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'shipinkechengList',
			component: shipinkechengList
		}, {
			path: 'shipinkechengDetail',
			component: shipinkechengDetail
		}, {
			path: 'shipinkechengAdd',
			component: shipinkechengAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'tuwenkechengList',
			component: tuwenkechengList
		}, {
			path: 'tuwenkechengDetail',
			component: tuwenkechengDetail
		}, {
			path: 'tuwenkechengAdd',
			component: tuwenkechengAdd
		}
		, {
			path: 'kechengleixingList',
			component: kechengleixingList
		}, {
			path: 'kechengleixingDetail',
			component: kechengleixingDetail
		}, {
			path: 'kechengleixingAdd',
			component: kechengleixingAdd
		}
		, {
			path: 'exampaperList',
			component: exampaperList
		}, {
			path: 'examrecordList',
			component: examrecordList
		}, {
			path: 'examfailrecord',
			component: examfailrecord
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
	, {
		path: '/examination',
		component: examination
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
